<template>
  <div id="app">
    <Header :subtitle="subtitle"/>
    <ImageList />
  </div>
</template>

<script>
  import Header from '@/components/Header.vue';
  import ImageList from '@/components/ImageList.vue';
  import { mapActions } from 'vuex';

  export default {
    name: 'Tournament',
    
    components: {
      Header,
      ImageList
    },

    data() {
        return {
            subtitle: '',
            tourID: null
        }
    },

    watch: {
      $route() {
        this.subtitle = this.$route.params.name;
        this.tourID = this.$route.params.id;

        this.fetchIDsByTournament(this.tourID);
      }
    },

    mounted() {
        this.subtitle = this.$route.params.name;
        this.tourID = this.$route.params.id;

        this.fetchIDsByTournament(this.tourID);
    },

    methods: {
      ...mapActions([
        'fetchIDsByTournament'
      ])
    }
  }
</script>

